html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family);
  -webkit-text-size-adjust: 100%;
  scroll-behavior: smooth;
}

.wrapper {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.content {
  flex: 1 0 auto;
}

.footer {
  flex: 0 0 auto;
}

body {
  height: 100%;
  font-size: 12px;
  font-weight: 400;
  line-height: 1.2;
  color: var(--white300);
  background-color: #2c2c2c;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  padding: 0;
  margin: 0;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
  display: block;
}


a {
  text-decoration: none;
  color: inherit;
  outline: transparent;
  transition: color 0.2s, background-color 0.2s, opacity 0.2s;
}

.logo {
  &:focus {
    box-shadow: 0 0 0 2px rgba(255, 104, 56, 0.7);
  }

  &:hover {
    opacity: 0.8;
  }

  &:active {
    opacity: 0.5;
  }
}

.is-hidden {
  display: none !important;
  /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
  appearance: none;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);

  max-width: var(--container-width);

  @include max($md) {
    padding: 0 var(--container-offsetMobile);
    max-width: var(--content-table);
  }

  @include max($ms) {
    max-width: var(--content-mobile);
  }

}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.burger {
  @include burger;
}

.tabs {
  @include tabs;
}

.main {
  &__inner {
    display: flex;
    justify-content: space-between;
  }
}

.delete-btn,
.add-btn {
  position: relative;
  width: 23px;
  height: 23px;

  &::after {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    background-image: url('../img/private-office-img/icon-delete.png');
    background-position: center center;
    background-repeat: no-repeat;
    width: 23px;
    height: 23px;
  }

}

.add-btn {

    width: 33px;
    height: 33px;
    border: 1px solid #FF4F00;

    &::after {
      transform: rotate(45deg);
      background-image: url('../img/private-office-img/icon-delete.png');
    }

  &--active {
    border: 0;
    &::after {
      background-image: url('../img/private-office-img/icon-add.png');
      transform: rotate(0);
    }
  }
}
