.dropdown {
  position: relative;
  width: 100%;
  &:not(:last-child) {
    margin-bottom: 10px;
  }
  &__button {
    position: relative;
    display: block;
    width: 100%;
    text-align: left;
    background-color: transparent;
    border: 2px solid var(--orange400);
    height: 36px;

    padding: 10px;

    font-weight: 700;
    font-size: 14px;
    line-height: 1;
    color: #FEFEFE;

    cursor: pointer;

    &:focus,
    &--active {
      outline: transparent;
      box-shadow: 0 0 0 3px rgba(255, 79, 0, 0.3);
    }

    &::after {
      content: "";
      position: absolute;
      top: 50%;
      right: 15px;
      transform: translateY(-50%);

      /* Рисуем треугольник */
      width: 0;
      height: 0;
      border-width: 6px 6px 0 6px;
      border-color: var(--orange400) transparent transparent transparent;
      border-style: solid;

      /* Снимаем клик с треугольника */
      pointer-events: none;
    }

    &::before {
      content: "";
      position: absolute;
      top: 0;
      bottom: 0;
      right: 23%;
      width: 2px;
      background-color: var(--orange400);
    }
  }

  &__list {
    display: none;

    position: absolute;
    left: 0;
    top: 48px;

    font-weight: 700;
    font-size: 14px;
    line-height: 1.14;
    color: #FEFEFE;

    background-color: var(--orange400);
    // border: 2px solid rgba(#FEFEFE, 0.5);
    overflow: hidden;

    width: 100%;

    z-index: 1;

    &--visible {
      display: block;
    }


  }

  &__list-item {
    margin: 0;
    padding: 0;
    border: 2px solid rgba(#FEFEFE, 0.5);
    padding: 10px;
    cursor: pointer;
    transition: background-color 0.2s;
    &:hover {
      background-color: #FEFEFE;
      color: var(--orange400);
    }
  }

  &__input-hidden {
    display: none;
  }
}
