.private-office {
  padding: 50px 0 160px;

  &__title {
    font-size: 40px;
    text-align: center;
    margin-bottom: 30px;
  }

  .tabs {
    display: flex;

    &__profile {
      background-color: var(--orange400);
      max-width: 213px;
      padding: 20px 20px 40px 20px;
      color: #2C2C2C;
      margin-right: 5px;
      height: 100%;
    }

    &__name {
      font-family: var(--neueMachina);
      font-weight: 800;
      font-size: 20px;
      text-transform: uppercase;
      border-bottom: 2px solid #2C2C2C;
      padding-bottom: 20px;
      text-align: center;
      margin-bottom: 20px;
    }

    &__nav {
      margin-bottom: 40px;
    }

    &__item {
      padding-bottom: 15px;
      border-bottom: 2px solid rgba(44, 44, 44, 0.25);

      &:not(:last-child) {
        margin-bottom: 15px;
      }
    }

    &__btn {
      font-weight: 700;
      font-size: 14px;
      line-height: 1.15;

      &--active {
        text-decoration: underline;
      }
    }

    &__exit {
      font-family: var(--neueMachina);
      font-weight: 800;
      font-size: 20px;
      padding-right: 28px;
      background-image: url('../img/private-office-img/icon-exit.png');
      background-repeat: no-repeat;
      background-position: right center;
      text-decoration-line: underline;
      text-transform: uppercase;
    }

    &__content {
      flex-grow: 1;
      border: 2px solid var(--orange400);
    }
  }

  .selected-products {

    &--none {
      display: none;
    }

    &__table {
      border-collapse: collapse;
      width: 100%;

      tr {
        border-bottom: 2px solid var(--orange400);
        text-align: center;
      }

      th,
      td {
        padding-top: 20px;
        padding-bottom: 20px;

        &:first-child {
          padding-left: 20px;
        }
      }

      th,
      .selected-products__price {
        color: var(--orange400);
        font-size: 16px;
        font-weight: 700;
        line-height: 1.12;
      }

      tbody {
        tr {
          border-top: none;
          border-bottom: none;
        }

        td {
          &:first-child {
            padding: 38px 50px 38px 20px;
            width: 442px;
          }
        }
      }
    }

    &__favorites {
      border-right: 2px solid var(--orange400);
      font-family: var(--neueMachina);
      font-weight: 800;
      text-align: left;
      color: #FEFEFE !important;
    }

    &__border {
      border-right: 2px solid var(--orange400);
    }

    &__price {
      font-family: var(--neueMachina);
      font-weight: 800;
    }

  }

  .personal-information {
    padding: 25px 25px 29px 25px;

    &__name {
      font-family: var(--neueMachina);
      font-weight: 800;
      text-transform: uppercase;
      font-size: 20px;
      margin-bottom: 14px;
    }

    &__inner {
      display: flex;
      align-items: flex-start;
    }

    &__avatar {
      margin-right: 13px;
    }

    &__content {
      font-size: 18px;
    }

    &__list {
      margin-bottom: 28px;
    }

    &__item {
      color: #C4C4C4;
      font-weight: 700;

      &:not(:last-child) {
        margin-bottom: 5px;
      }
    }

    &__text {
      color: #FEFEFE;
    }

    &__balance {
      color: var(--orange400);
      padding-right: 19px;
      background-image: url('../img/private-office-img/icon-arrow-right.png');
      background-position: right center;
      background-repeat: no-repeat;
    }

    &__element {
      &:not(:last-child) {
        margin-bottom: 10px;
      }
    }

    &__link {
      color: #FEFEFE;
      font-weight: 700;
    }
  }

  .garage {
    padding: 25px 70px 29px 25px;
    &__name {
      font-family: var(--neueMachina);
      font-weight: 800;
      text-transform: uppercase;
      font-size: 20px;
      margin-bottom: 22px;
    }

    &__table {
      border-collapse: collapse;
      width: 100%;
      margin-bottom: 16px;
      th, td {
        border-bottom: 2px solid rgba(255, 79, 0, 0.3);
      }

      th {
        padding-bottom: 9px;
        text-align: left;
      }

      td {
        padding-top: 14px;
        padding-bottom: 14px;
        font-family: var(--neueMachina);
        font-weight: 800;
        font-size: 12px;
      }

    }

    &__heading {
      font-size: 18px;
      font-weight: 700;
    }

    &__img {
      width: 48px;
      height: 47px;
      background-color: #C4C4C4;
    }

    .additional-options {
      display: flex;
      justify-content: space-between;
      padding-right: 23px;
      &__content {
        max-width: 180px;
        width: 100%;
      }

      &__group, &__list {
        margin-bottom: 30px;
      }

      &__input {
        width: 100%;
        background-color: transparent;
        border: 2px solid var(--orange400);
        height: 36px;
        padding: 10px;
        font-weight: 700;
        font-size: 14px;
        line-height: 1;
        color: #FEFEFE;
        &::placeholder {
          color: #FEFEFE;
          opacity: 1;
        }
      }

      &__item {
        &:not(:last-child) {
          margin-bottom: 10px;
        }
      }

      &__checkbox {

        .custom-checkbox {

          &__content {
            font-size: 14px;
            font-weight: 700;
            &::before {
              width: 15px;
              height: 15px;
              border: 0;
              margin-right: 10px;
              background-color: var(--orange400);
            }
          }
        }
      }

      &__file {
        background-color: transparent;
        border: 2px solid var(--orange400);
        color: #FEFEFE;
        padding-left: 20px;
        padding-right: 20px;
        font-size: 14px;
        font-weight: 700;
        margin-bottom: 5px;
        &:focus {
          box-shadow: 0px 0px 0px 2px rgba(230, 56, 4, 0.7);
        }

        &:hover {
          background-color: darken(#FF4F00, 15%);
        }

        &:active {
          background-color: transparent;
          color: #fff;
        }
      }

      &__text {
        margin-bottom: 15px;
        font-size: 14px;
        font-weight: 700;
        color: #707070;
        white-space: nowrap;
      }

      &__save {
        padding-top: 12px;
        padding-bottom: 12px;
      }
    }
  }

  .history-order {
    padding: 25px 25px 29px 25px;

    &__name {
      font-family: var(--neueMachina);
      font-weight: 800;
      text-transform: uppercase;
      font-size: 20px;
      margin-bottom: 22px;
    }

    &__table {
      border-collapse: collapse;
      width: 100%;
      text-align: end;
    }
  }

}
